<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">发起审批</div>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item v-if="pageType==2" :to="{ path: '/home/investProjectManageHomepage' ,query: {id:projectId}}">返回项目首页</el-breadcrumb-item>
              <el-breadcrumb-item :to="{ path: '/home/applyListNew' ,query: {projectId:projectId,from:pageType}}">审批列表</el-breadcrumb-item>
              <el-breadcrumb-item>发起审批</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="line"></div>
        </div>
        <div class="createapply-home-icongroup" v-for="item in applyTypelist">
          <div class="createapply-home-icongroup-title">
            {{item.title}}
          </div>
          <div class="createapply-home-icon-itemlist">
            <div class="createapply-home-iconitem" @click="gotoCreateApply(item1,item.type)" :class="{'createapply-home-iconitem-disabled':item1.disabled}" v-for="item1 in item.children">
              <img :src="item1.url" class="">
              <div>{{item1.name}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//审批流程状态
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
import { getApplyTypeNew } from '../../config/api'
export default {
  data() {
    return {
      pageType: 1, //1=>项目列表直接进入2=>项目管理页面进入3=>行政管理页面进入
      applyTypelist: [],
      disabledIdlist: []
    }
  },
  created() {
    if (!this.$route.query.from) {} else {
      this.pageType = this.$route.query.from;
    }
    if (!this.$route.query.projectId) {
      this.projectId = '';
      // this.$router.push({
      //   name: 'investProjectManageList'
      // })
    } else {
      this.projectId = this.$route.query.projectId
    }
    //获取审批类型
    getApplyTypeNew({}).then(res => {
      if (res.data.msgNo == 1) {
        let tl = res.data.payload;
        this.applyTypelist = res.data.payload;
      }
    })
  },
  components: {},
  computed: {},
  mounted() {},
  methods: {
    gotoCreateApply(item,aType) {
      this.$router.push({
        name: 'createApplyNew',
        query: {
          template_id: item.id,
          aType: aType,
          aName: item.name,
          from: this.pageType,
          projectId: this.projectId
        }
      })
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.historyapply-group {
  width: 100%;
  border: 1px solid #E4E4E4;
  padding: 18px 20px 16px;
}

.table-group-title {
  padding-left: 0px;
}

</style>



